// common variables
$primary-medium: #005888;
$primary-dark: #024e79;
$background-gray: #efefef;
$main-test-card-white: #f9f9f9;
$error-red: #980E1F;
$success-green: #447400;


// button styles
button.btn-primary {
  background-color: $primary-medium;
  border-color: $primary-medium;
}

.WBBCard {
  border: none;
  .card-header {
    background-color: $primary-medium;
    color: white;
  }
  .card-header:first-child {

  }
  h2 {
    margin-bottom: 0;
  }
  .card-body {
    background-color: $background-gray;
  }
}

$main-card-border-radius: 20px;

.WBBMainTestCard {
  .card-header {
    border-top-left-radius: $main-card-border-radius;
    border-top-right-radius: $main-card-border-radius;
  }
  > .card-body {
    border-bottom-left-radius: $main-card-border-radius;
    border-bottom-right-radius: $main-card-border-radius;
    padding-bottom: 40px;
  }
  .WBBLatencySummary {
    background-color: $main-test-card-white;
    p {
      margin-bottom: 0;
    }
    .condition {
      font-size: 16px;
    }
    .latency {
      &.good {
        color: $success-green;
      }
      &.bad {
        color: $error-red;
      }
      margin-top: 10px;
      font-size: 20px;
    }
  }
  .spacer {
    height: 5px;
    background-color: white;
    margin-left: 10px;
  }
  .WBBCardTitle {
    height: 70px;
    text-align: center;
    h2 {
      margin-top: 30px;
    }
  }
  .WBBCardTitle.WBBTestingDone {
    height: auto;
    text-align: left;
    h1 {
      margin-left: 10px;
    }
    .indicator {
      img {
        margin-right: 5px;
      }
      margin-left: 20px;
    }
  }
}
